const models = {
    //CST
    formatCSTDate: function (strDate, format) {
        return this.formatDate(new Date(strDate), format);
    },
    //,
    formatDate: function (date, format) {
        var paddNum = function (num) {
            num += "";
            return num.replace(/^(\d)$/, "0$1");
        }
        //
        var cfg = {
            yyyy: date.getFullYear() // : 4
            , yy: date.getFullYear().toString().substring(2)// : 2
            , M: date.getMonth() + 1  // : 10
            , MM: paddNum(date.getMonth() + 1) // : 10
            , d: date.getDate()   // : 10
            , dd: paddNum(date.getDate())// : 10
            , hh: date.getHours()  //
            , mm: date.getMinutes() //
            , ss: date.getSeconds() //
        }
        format || (format = "yyyy-MM-dd hh:mm:ss");
        return format.replace(/([a-z])(\1)*/ig, function (m) { return cfg[m]; });
    },
    //
    getYesterday: function (date) {
        var yesterday_milliseconds = date.getTime() - 1000 * 60 * 60 * 24;
        var yesterday = new Date();
        yesterday.setTime(yesterday_milliseconds);
        return yesterday;
    },
    //
    getLastMonth: function (date) {
        var daysInMonth = new Array([0], [31], [28], [31], [30], [31], [30], [31], [31], [30], [31], [30], [31]);
        var strYear = date.getFullYear();
        var strDay = date.getDate();
        var strMonth = date.getMonth() + 1;
        if (strYear % 4 == 0 && strYear % 100 != 0) {
            daysInMonth[2] = 29;
        }
        if (strMonth - 1 == 0) {
            strYear -= 1;
            strMonth = 12;
        }
        else {
            strMonth -= 1;
        }
        strDay = daysInMonth[strMonth] >= strDay ? strDay : daysInMonth[strMonth];
        return new Date(strYear, strMonth, strDay);
    },
    //
    getLastYear: function (date) {
        var strYear = date.getFullYear() - 1;
        var strDay = date.getDate();
        var strMonth = date.getMonth() + 1;
        return new Date(strYear, strMonth, strDay);
    }
}
export default models;



